<template>
	<view>
		
			<view class="cu-form-group">
				<view class="title">标题</view>
				<input type="text" placeholder="请输入标题" v-model="biaoti" />
			</view>
			
			<view class="cu-form-group">
				<view class="title">姓名</view>
				<input type="text" placeholder="请输入姓名" v-model="post_huzu" />
			</view>
			
			<view class="cu-form-group">
				<view class="title">手机</view>
				<input type="number" placeholder="请输入手机" v-model="post_shouji" />
			</view>
		
			<view class="cu-form-group align-start" style="height: 50vw;">
				<view class="title">描述</view>
				<textarea style="height: calc(50vw - 10vw);" maxlength="-1" @input="textareaBInput" placeholder="填写信息描述"></textarea>
			</view>
		
			<view class="cu-bar bg-white margin-top">
				<view class="action">图片上传</view>
				<view class="action">{{ imgList.length }}/9</view>
			</view> 
			<view class="cu-form-group">
				<view class="grid col-4 grid-square flex-sub">
					<view class="bg-img" v-for="(item, index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
						<image :src="imgList[index]" mode="aspectFill"></image>
						<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index"><text class="cuIcon-close"></text></view>
					</view>
					<view class="solids" @tap="ChooseImage" v-if="imgList.length < 9"><text class="cuIcon-cameraadd"></text></view>
				</view>
				
			</view>
			
			<view @click="tijiao_baoxiu_xinxi()" class="huxing_quan" style="margin-top: 5vw;margin-left: 5vw;width: 90vw;height: 10vw;background: #518bee;text-align: center;color: #FFFFFF;line-height: 10vw;font-size: 110%;"><text>提交报修</text></view>
			
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgList: [],
				xiangxi_miaoshu:"",
				biaoti:"",
				
				post_huzu: "",
				post_shouji: "",
				
			}
		},
		onLoad() {
			
			let _this = this
			let data = {}
			_this.wangluo_qingqiu('/wx/ipms/getcustomerdata', data, true, true, 'GET').then(value => {
				if (value) {
					_this.post_huzu = value.customerName
					_this.post_shenfen = value.idCard
					_this.post_shouji = value.mobilePhone
				}
			})
			
		},
		methods: {
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '提示',
					content: '确定要删除吗？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1);
						}
					}
				});
			},
			ChooseImage() {
				uni.chooseImage({
					count: 9, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: res => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths);
						} else {
							this.imgList = res.tempFilePaths;
						}
					}
				});
			},
			textareaBInput(e) {
				this.xiangxi_miaoshu = e.detail.value;
			},
			tijiao_baoxiu_xinxi:function(){
				let _this = this
				
				if (!_this.post_huzu) {
					uni.showModal({
						title: '提示',
						content: "姓名不能为空",
						showCancel: false,
						success: function() {}
					});
					return false;
				}
				
				if (!(/^[0-9]{11}$/.test(_this.post_shouji))) {
					uni.showModal({
						title: '提示',
						content: "手机号有误，请重填",
						showCancel: false,
						success: function() {}
					});
					return false;
				}
				
				if (!_this.biaoti) {
					uni.showModal({
						title: '提示',
						content: "标题不能为空",
						showCancel: false,
						success: function() {}
					});
					return false;
				}
							
				if (!_this.xiangxi_miaoshu) {
					uni.showModal({
						title: '提示',
						content: "描述不能为空",
						showCancel: false,
						success: function() {}
					});
					return false;
				}			
				
				if (_this.imgList.length<1) {
					uni.showModal({
						title: '提示',
						content: "请补充问题图片",
						showCancel: false,
						success: function() {}
					});
					return false;
				}	
				
				

				
				
				let data = {
					customerName: _this.post_huzu,
					mobilePhone: _this.post_shouji,
					feedbackTitle:_this.biaoti,
					feedbackMsg:_this.xiangxi_miaoshu,
					
				}
				_this.wangluo_qingqiu('/wx/ipms/submitFeedback', data, true, true, 'POST').then(value => {
					if (value) {
						
						let tupian_geshu  = _this.imgList.length
						let tupian_chengong = 0
						for(let i = 0;i<tupian_geshu;i++){
							// console.log(_this.imgList[i])
							uni.showLoading({
								title: '上传图片中'
							});
							uni.uploadFile({
								url: getApp().globalData.public_server_url + '/wx/file/fileupload', //仅为示例，非真实的接口地址
								filePath:_this.imgList[i],
								name: 'file',
								header: {
									'token': getApp().globalData.public_user_token,
								},
								formData: {
									bizType:1,
									bizUid:value.feedbackId,
								},
								success: (uploadFileRes) => {
									uni.hideLoading();
									let fanhui = JSON.parse(uploadFileRes.data)
									if (fanhui.code == 0) {
										console.log(fanhui.data)
									}
									
									tupian_chengong +=1
									if(tupian_chengong==tupian_geshu){
										uni.showModal({
											title: '提示',
											content: "反馈完成，等待处理。",
											showCancel: false,
											success: function() {
												uni.navigateBack({
													
												})
											}
										});
									}
									
								}
							});
						}
						
						


					}
				})
				
				
			}
		}
	}
</script>

<style>
	page {
		background: #FFFFFF;
	}
</style>
